* {
  margin: 0;
  font-size: 13px;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif
}

.app {
  width: 100dvw;
  height: 100dvh;
  background-color: whitesmoke;
}

.login {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.panel {
  width: 60%;
  height: 20%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.credential {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
}

.credential input {
  padding: 5px 10px;
  width: 250px;
  height: 40px;
  border: none;
  border-radius: 5px;
  background-color: white;
  box-sizing: border-box;
  outline: none;
}

input:focus {
  border-bottom: 2px solid blue;
}

.panel button {
  padding: 5px 10px;
  width: 250px;
  height: 50px;
}

.login-error {
  color: red;
}

.chat {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}

.header {
  width: 100%;
  height: 10%;
  border: 1px dotted black;
  box-sizing: border-box;
}

.user-conversations {
  width: 100%;
  height: 90%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.users {
  width: 30%;
  height: 100%;
  border-right: 1px dotted black;
  box-sizing: border-box;
  padding: 10px;
}

.user {
  border-radius: 5px;
  padding: 5px 10px;
  box-sizing: border-box;
  cursor: pointer;
}

.conversation {
  width: 70%;
  height: 100%;
  /* border: 1px dotted black; */
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}

.message-display {
  width: 100%;
  height: 85%;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: flex-end;
  box-sizing: border-box;
  padding: 10px;
}

.typing {
  font-size: smaller;
  padding: 10px 5px;
  background-color: white;
}

.message-layer {
  width: 100%;
}

.message {
  max-width: 80%;
  min-height: 5%;
  word-wrap: break-word;
  border-radius: 5px;
  padding: 5px 15px;
  background-color: white;
  box-sizing: border-box;
  margin: 3px 0;
}

.user-input {
  width: 100%;
  height: 15%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 10px;
  box-sizing: border-box;
}

.user-input textarea {
  width: 75%;
  height: 50%;
  box-sizing: border-box;
  outline: none;
  padding: 10px;
  overflow: hidden;
  resize: none;
  border: none;
  border-radius: 5px;
}

.user-input textarea:focus {
  border-bottom: 2px solid blue;
}

.user-input button {
  width: 20%;
  height: 50%;
}

button {
  outline: none;
  cursor: pointer;
  border: none;
  border-radius: 5px;
  background-color: white;
  box-sizing: border-box;
}